@import '@fluentui/react/dist/sass/_References';

.modalWrapper {
	display: flex;
	width: 600px;
	flex-direction: column;
	flex: 1;
}

.modalHeader {
	padding: 40px 30px;

	.title {
		font-size: 24px;
		font-weight: 600;
		color: $ms-color-themePrimary;
	}
}

.modalBody {
	padding: 10px 30px;
	flex: 1;
	display: flex;
	flex-direction: column;

	.detailsGroup {
		display: flex;
		flex-direction: row;

		.ms-TextField {
			flex: 2;
			margin-right: 20px;
		}

		.ms-Dropdown-container {
			margin-top: 24px;
			flex: 1;
		}
	}

	.ms-TextField {
		margin-bottom: 15px;

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.modalFooter {
	padding: 40px 30px;
	display: flex;
	flex-direction: row;

	button {
		&:first-child {
			margin-right: 10px;
		}
	}
}
